import {GetDetails, GetShortDetailsInScrimShort} from '../plugins/getDetails/getDetails'
import ScrimWithDetail from '../plugins/scrimWithDetail/scrimWithDetail'
import Vue from 'vue'

export default {
	state: {
		baseDetails: [],
		rotatedDetails: [],
		detailDirection: 'getSizes',
		showRotated: true,
		shortDetailsInScrimShort: {},
		longDetailsInScrimShort: {},
		rotatedDetailCount: 0, // todo временное решение убрать при рефакторинге
		detailsInScrim: {},
		scrimIndent: {
			name: '',
			values: {
				top: 0,
				right: 0,
				bottom: 0,
				left: 0
			}
		},
		detail: {
			width: 187,
			height: 60,
			indents: {
				left: {
					name: 'Слева',
					value: 4
				},
				right: {
					name: 'Справа',
					value: 4
				},
				bottom: {
					name: 'Снизу',
					value: 4
				},
				top: {
					name: 'Сверху',
					value: 4
				}
			}
		},
		scrim: {
			width: 500,
			height: 700,
			offsetWidth: 0,
			offsetHeight: 0
		},
		indentse: [
			{
				name: 'Слева',
				indentDirection: 'indentLeft',
				value: 2
			},
			{
				name: 'Сверху',
				indentDirection: 'indentTop',
				value: 2
			},
			{
				name: 'Справа',
				indentDirection: 'indentRight',
				value: 2
			},
			{
				name: 'Снизу',
				indentDirection: 'indentBottom',
				value: 2
			},
		],
	},
	mutations: {
		setIndentPlus(state, payload) {
			state.detail.indents[payload].value++
		},
		setIndentMin(state, payload) {
			if (state.detail.indents[payload].value > 0) state.detail.indents[payload].value--
		},
		countPlus(state, element) {
			switch (element){
				case 'detailHeight' :
					state.detail.height++
					break;
				case 'detailWidth' :
					state.detail.width++
					break;

				// todo если все ок удалить
				// case 'indentLeft' :
				// 	state.detail.indents.left.value++
				// 	break;
				// case 'indentTop' :
				// 	state.detail.indents.top.value++
				// 	break;
				// case 'indentRight' :
				// 	state.detail.indents.right.value++
				// 	break;
				// case 'indentBottom' :
				// 	state.detail.indents.bottom.value++
				// 	break;
				case '_inputScrimHeight' :
					state.scrim.height++
					break;
				case '_inputScrimWidth' :
					state.scrim.width++
					break;
				default:
					console.log(`Error: countPlus елемент: ${element} не найден`);
					return false
			}
		},
		countMin(state, element){
			switch (element){
				case 'detailHeight' :
					if (state.detail.height > 1) state.detail.height--
					break;
				case 'detailWidth' :
					if (state.detail.width > 1) state.detail.width--
					break;

				// todo если все ок удалить
				// case 'indentLeft' :
				// 	if (state.detail.indents.left.value > 0) state.detail.indents.left.value--
				// 	break;
				// case 'indentTop' :
				// 	if (state.detail.indents.top.value > 0) state.detail.indents.top.value--
				// 	break;
				// case 'indentRight' :
				// 	if (state.detail.indents.right.value > 0) state.detail.indents.right.value--
				// 	break;
				// case 'indentBottom' :
				// 	if (state.detail.indents.bottom.value > 0) state.detail.indents.bottom.value--
				// 	break;
				case '_inputScrimHeight' :
					if (state.scrim.height > 0) state.scrim.height--
					break;
				case '_inputScrimWidth' :
					if (state.scrim.width > 0) state.scrim.width--
					break;
				default:
					console.log(`Error: countPlus елемент: ${element} не найден`);
					return false
			}
		},

		reloadDetailsInScrim(state) {
			let ScrimWidthDetail = new ScrimWithDetail({
				scrimWidth: state.scrim.width,
				scrimHeight: state.scrim.height,

				offsetWidth: Number(state.scrim.offsetWidth),
				offsetHeight: Number(state.scrim.offsetHeight),

				detailWidth: state.detail.width,
				detailHeight: state.detail.height,

				detailIndentLeft: state.detail.indents.left.value,
				detailIndentTop: state.detail.indents.top.value,
				detailIndentRight: state.detail.indents.right.value,
				detailIndentBottom: state.detail.indents.bottom.value,

				showRotated: state.showRotated
			})
			state.detailsInScrim = ScrimWidthDetail[state.detailDirection]
		},
		createBaseDetails(state) {
			let totalBaseDetails = state.detailsInScrim.details.baseDetail.totalCount
			let baseDetail = state.detailsInScrim.details.baseDetail

			let isRotated = state.detailsInScrim.details.rotatedDetail.totalCount

			let countInModShortSide = state.detailsInScrim.details.rotatedDetail.countInModShortSide
			let countInModLongSide = state.detailsInScrim.details.rotatedDetail.countInModLongSide

			let subDetailsCount

			// формируем массив основных деталей
			if (isRotated) {
				if (countInModShortSide) {
					subDetailsCount = baseDetail.atLongSide
				}
				if (countInModLongSide) {
					subDetailsCount = baseDetail.atShortSide
				}
			} else {
				subDetailsCount = baseDetail.atLongSide + baseDetail.atShortSide + 1
			}

			state.baseDetails = []

			for (let i = 0; i < totalBaseDetails + subDetailsCount; i++){
				state.baseDetails.push({
					width: baseDetail.width,
					height: baseDetail.height,

					borderLeftWidth: state.detail.indents.left.value,
					borderTopWidth: state.detail.indents.top.value,
					borderRightWidth: state.detail.indents.right.value,
					borderBottomWidth: state.detail.indents.bottom.value,
					class: '',
					rotated: false
				})

				// проверка где количество деталей
				// в избытке (по вертикали или по горизонтали)
				// чтобы повешать класс
				if (!countInModLongSide) {
					if ((i+1) % (baseDetail.atShortSide+1) === 0 && i > 0) {
						state.baseDetails[i].class = 'pseudo'
					}
					if (!isRotated) {
						if (i >= totalBaseDetails + baseDetail.atLongSide) {
							state.baseDetails[i].class = 'pseudo'
						}
					}
				} else {
					if (i >= totalBaseDetails) {
						state.baseDetails[i].class = 'pseudo'
					}
				}
			}
		},
		createRotatedDetails(state) {
			state.rotatedDetails = []

			let totalRotatedDetails = state.detailsInScrim.details.rotatedDetail.totalCount
			let rotatedDetail = state.detailsInScrim.details.rotatedDetail
			let subDetailsCount = 0

			let countInModShortSide = state.detailsInScrim.details.rotatedDetail.countInModShortSide
			let countInModLongSide = state.detailsInScrim.details.rotatedDetail.countInModLongSide
			let rotatedLongSide = state.detailsInScrim.details.rotatedDetail.atLongSide
			let rotatedShortSide = state.detailsInScrim.details.rotatedDetail.atShortSide


			let rotatedTotalCount = state.detailsInScrim.details.rotatedDetail.totalCount

			if (countInModShortSide) {
				subDetailsCount = countInModShortSide + rotatedShortSide + 1
			}
			if (countInModLongSide) {
				subDetailsCount = countInModLongSide + rotatedLongSide + 1
			}
			if (totalRotatedDetails) {
				for (let i = 0; i < totalRotatedDetails + subDetailsCount; i++){
					state.rotatedDetails.push({
						width: rotatedDetail.width,
						height: rotatedDetail.height,

						borderLeftWidth: state.detail.indents.left.value,
						borderTopWidth: state.detail.indents.top.value,
						borderRightWidth: state.detail.indents.right.value,
						borderBottomWidth: state.detail.indents.bottom.value,
						class: '',
						rotated: false
					})
					if (countInModShortSide) {
						if ((i+1) % (rotatedDetail.atShortSide + 1) === 0 && i > 0) {
							state.rotatedDetails[i].class = 'pseudo'
						}
						if (i >= rotatedTotalCount + countInModShortSide) {
							state.rotatedDetails[i].class = 'pseudo'
						}
					}
					if (countInModLongSide) {
						if ((i+1) % (countInModLongSide + 1) === 0 && i > 0) {
							state.rotatedDetails[i].class = 'pseudo'
						}
						if (i >= rotatedDetail.totalCount + subDetailsCount - countInModLongSide - 1) {
							state.rotatedDetails[i].class = 'pseudo'

						}
					}
				}
			}

		},

		setDetailSizes(state, payload) {
			state.detail = payload
		},
		setDetailDirection(state, payload) {
			state.detailDirection = payload
		},
		setDetailHeight(state, payload) {
			Vue.set(state.detail, 'height', parseInt(payload))
		},
		setDetailWidth(state, payload) {
			Vue.set(state.detail, 'width', parseInt(payload))
		},

		setIndent(state, payload) {
			state.detail.indents[payload.indent].value = parseInt(payload.value)
		},

		setScrimSizes(state, payload) {
			state.scrim = payload
		},
		setScrimWidth(state, payload) {
			state.scrim.width = payload
		},
		setScrimHeight(state, payload) {
			state.scrim.height = payload
		},
		setScrimOffsets(state, payload) {
			state.scrim.offsetHeight = payload.values.top + payload.values.bottom
			state.scrim.offsetWidth = payload.values.left + payload.values.right
		},
		setShowRotated(state, payload) {
			state.showRotated = payload
		},
		setScrimIndent(state, payload) {
			state.scrimIndent = payload
		}
	},
	actions: {
		setDetailSizes({commit}, payload) {
			commit('setDetailSizes', payload)
		},
		changeScrimOffset({commit, state}, payload) {
			let scrimIndents = {
				width: Number(payload.values.right) + Number(payload.values.left),
				height: Number(payload.values.bottom) + Number(payload.values.top),
			}
			let scrimSizes = {
				width: state.scrim.width,
				height: state.scrim.height,
				offsetWidth: scrimIndents.width,
				offsetHeight: scrimIndents.height
			}
			commit('setScrimSizes', scrimSizes)
		},
		setDetailHeight({commit, dispatch}, payload) {
			commit('setDetailHeight', payload)
			dispatch('reloadDetailsInScrim', payload)
		},
		setDetailWidth({commit, dispatch}, payload) {
			commit('setDetailWidth', payload)
			dispatch('reloadDetailsInScrim', payload)
		},

		setIndent({commit, dispatch}, payload) {
			commit('setIndent', payload)
			dispatch('reloadDetailsInScrim', payload)
		},
		setIndents({commit, dispatch, state}, payload) {
			for (let indent in state.detail.indents){
				commit('setIndent', {value: payload, indent: indent})
			}
			dispatch('reloadDetailsInScrim', payload)
		},

		setScrimSizes({commit}, payload) {
			commit('setScrimSizes', payload)
		},
		setScrimWidth({commit}, payload){
			commit('setScrimWidth', payload)
		},
		setScrimHeight({commit}, payload){
			commit('setScrimHeight', payload)
		},

		createBaseDetails({commit}, payload) {
			commit('createBaseDetails', payload)
		},

		createRotatedDetails({commit}, payload) {
			commit('createRotatedDetails', payload)
		},

		countMin({commit, dispatch}, payload) {
			dispatch('reloadDetailsInScrim', payload)
			commit('countMin', payload)
		},
		countPlus({commit, dispatch}, payload) {
			dispatch('reloadDetailsInScrim', payload)
			commit('countPlus', payload)
		},
		setIndentPlus({commit, dispatch}, payload) {
			commit('setIndentPlus', payload)
			dispatch('reloadDetailsInScrim', payload)
		},
		setIndentMin({commit, dispatch}, payload) {
			commit('setIndentMin', payload)
			dispatch('reloadDetailsInScrim', payload)
		},
		reloadDetailsInScrim({commit}, payload) {
			commit('reloadDetailsInScrim', payload)
			commit('createBaseDetails', payload)
			commit('createRotatedDetails', payload)
		},
		setScrimOffsets({commit}, payload) {
			commit('setScrimOffsets', payload)
			commit('reloadDetailsInScrim')
		},
		setDetailDirection({commit, dispatch}, payload) {
			commit('setDetailDirection', payload)
			if (payload === 'getSizes') commit('setShowRotated', true)
			dispatch('reloadDetailsInScrim', payload)
		},
		setShowRotated({commit, dispatch}, payload) {
			commit('setShowRotated', payload)
			dispatch('reloadDetailsInScrim')
		},
		setScrimIndent({commit, dispatch}, payload) {
			commit('setScrimIndent', payload)
			dispatch('reloadDetailsInScrim')
		}
	},
	getters: {
		baseDetails(state) {
			return state.baseDetails
		},
		rotatedDetails(state) {
			return state.rotatedDetails
		},
		getDetailsInScrim(state) {
			return state.detailsInScrim
		},

		getShortDetailsInScrimShortTotal(state) {
			return state.shortDetailsInScrimShort
		},
		detail(state) {
			return state.detail
		},
		indents(state) {
			return state.detail.indents
		},
		scrim(state) {
			return state.scrim
		},
		getRotatedDetailCount(state) {
			return state.rotatedDetailCount
		},
		detailDirection(state) {
			return state.detailDirection
		},
		showRotated(state) {
			return state.showRotated
		},
		scrimIndent(state) {
			return state.scrimIndent
		}
	}
}