export default {
	state: {
		user: {
			isAuth: false,
			uid: null
		},
		unsubscribeAuth: null,
	},
	mutations: {
		setUserState(state, payload) {
			state.user.isAuth = true
			state.user.uid = payload
		},
		unSetUserState(state) {
			state.user = {
				isAuth: false,
				uid: null
			}
		},
		setUnsubscribeAuth(state, payload) {
			state.unsubscribeAuth = payload
		},
	},
	actions: {
		initAuth({dispatch, commit, state}, payload) {
			if(state.unsubscribeAuth) {
				state.unsubscribeAuth()
			}
			return new Promise((resolve, reject)=>{
				let unsubscribe = firebase.auth().onAuthStateChanged(user => {
					dispatch('stateChanged', user)
					resolve(user)
				})
				commit('setUnsubscribeAuth', unsubscribe)
			})
		},
		createDefaultSizes({commit, dispatch, state}, userId) {
			Vue.$db.collection('userData').doc(userId).set(defaultSettingsNewUser)
		},
		registrationUser({commit, dispatch}, payload) {
			commit('setProcessingToState', true)
			firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
				.then((inp) => {
					commit('setProcessingToState', false)
					commit('clearErrorState')
					dispatch('createDefaultSizes', inp.user.uid)
				})
				.catch(function(error) {
					commit('setProcessingToState', false)
					commit('setErrorToState', error)
			});
		},
		signIn({commit}, payload) {
			firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
				.then(() => {
					commit('setProcessingToState', false)
					commit('clearErrorState')
				})
				.catch(function(error) {
					commit('setProcessingToState', false)
					commit('setErrorToState', error)
			});
		},
		signOut() {
			firebase.auth().signOut()
		},
		stateChanged({commit, dispatch}, payload) {
			if(payload) {
				commit('setUserState', payload.uid)
				dispatch('loadUserData', payload.uid)
				dispatch('loadTemplateScrims', payload.uid)
			} else {
				commit('unSetUserState')
			}
		},
		sendUserData({dispatch, state}, payload) {
			const user = state.user
			Vue.$db.collection('userData')
				.doc(user.uid)
				.set(payload, { merge: true })
				.then(()=>{
					dispatch('stateChanged', user)
				})
		}
	},
	getters: {
		getUserAuth(state) {
			return state.user.isAuth
		},
		getUserUid(state) {
			return state.user.uid
		}
	}

}