import Vue from 'vue'

const defaultUserData = {
	scrims: [],
	details: []
}

export default {
	state: {
		userData: defaultUserData
	},
	mutations: {
		setUserDataToState(state, payload) {
			state.userData = payload
		}
	},
	actions: {
		loadUserData({commit}, payload) {
			commit('setProcessingToState', true)
			let userDataRef = Vue.$db.collection('userData').doc(payload)
			userDataRef.get().then((data) => {
				let userData = data.exists ? data.data() : defaultUserData
				if (!userData.scrim) {
					userData.scrim = {}
				}

				commit('setDetailSizes', userData.details[0])
				commit('setScrimSizes', userData.scrims[0])

				commit('setUserDataToState', userData)
				commit('setProcessingToState', false)
			}).catch((error) => {
				console.log(error)
				commit('setProcessingToState', false)
			})
		},

	},
	getters: {
		getUserData(state){
			return state.userData
		}
	}
}