import StoreScrimAndDetail from './storeScrimAndDetail';
const defaultRotatedDetail = {
	atShortSide: 0,
	atLongSide: 0,

	totalCount: 0,

	countInModShortSide: 0,
	countInModLongSide: 0,

	height: 0,
	width: 0,
}
export default class BaseDetails extends StoreScrimAndDetail {
	constructor(props){
		super(props)
		let showRotated = props.showRotated
		this._baseDetail = this._computeBaseDetails()
		this._rotatedDetail = showRotated ? this._computeRotatedDetails() : defaultRotatedDetail
		this._scrim.mods = this._computeMods()

		this._baseDetailVertical = this._computeBaseDetailsInVertical()
		this._rotatedDetailVertical = showRotated ? this._computeRotatedDetailsInVertical() : defaultRotatedDetail

		this._baseDetailHorizontal = this._computeBaseDetailsInHorizontal()
		this._rotatedDetailHorizontal = showRotated ? this._computeRotatedDetailsInHorizontal() : defaultRotatedDetail
	}
	_computeVerticalBaseDetail() {
		let detailShortSide = this.detail.computedShortSide
		let detailLongSide = this.detail.computedLongSide

		let detailHeight = detailLongSide
		let detailWidth = detailShortSide

		let atShortSide = Math.floor(this._scrim.computedShortSide / detailWidth)
		let atLongSide = Math.floor(this._scrim.computedLongSide / detailHeight)

		let countInModBottom = BaseDetails.computeCountInMod(this._scrim.computedLongSide, detailHeight, detailWidth)

		let countRotatedInLongSide = Math.floor(this._scrim.computedShortSide / detailLongSide)

		let totalCountRotated = BaseDetails.computeTotalCountInScrim(countInModBottom, countRotatedInLongSide)
		let totalCountBase = BaseDetails.computeTotalCountInScrim(atShortSide, atLongSide)

		let totalCount = totalCountRotated + totalCountBase

		return {
			atShortSide: atShortSide,
			atLongSide: atLongSide,

			height: detailHeight,
			width: detailWidth,

			totalCount: totalCountBase,
			totalCountAll: totalCount,
			test: this._rotatedDetail,

		}
	}
	_computeHorizontalBaseDetail() {
		let detailShortSide = this.detail.computedShortSide
		let detailLongSide = this.detail.computedLongSide

		let detailHeight = detailShortSide
		let detailWidth = detailLongSide

		let atShortSide = Math.floor(this._scrim.computedShortSide / detailWidth)
		let atLongSide = Math.floor(this._scrim.computedLongSide / detailHeight)

		let countInModRight = BaseDetails.computeCountInMod(this._scrim.computedShortSide, detailWidth, detailHeight)

		let countRotatedInLongSide = Math.floor(this._scrim.computedLongSide / detailLongSide)

		let totalCountRotated = BaseDetails.computeTotalCountInScrim(countInModRight, countRotatedInLongSide)
		let totalCountBase = BaseDetails.computeTotalCountInScrim(atShortSide, atLongSide)

		let totalCount = totalCountRotated + totalCountBase

		return {
			atShortSide: atShortSide,
			atLongSide: atLongSide,

			totalCount: totalCountBase,
			totalCountAll: totalCount,

			height: detailHeight,
			width: detailWidth,

			countInModRight: countInModRight,

			test: this._rotatedDetail
		}
	}
	_computeBaseDetails() {
		return this._computeVerticalBaseDetail().totalCountAll > this._computeHorizontalBaseDetail().totalCountAll ?
			this._computeVerticalBaseDetail() :
			this._computeHorizontalBaseDetail()
	}
	_computeBaseDetailsInVertical () {
		let atShortSide = Math.floor(this._scrim.computedShortSide / this.detail.computedShortSide)
		let atLongSide = Math.floor(this._scrim.computedLongSide / this.detail.computedLongSide)
		return {
			atShortSide: atShortSide,
			atLongSide: atLongSide,
			totalCount: BaseDetails.computeTotalCountInScrim(atShortSide, atLongSide),
			height: this.detail.computedLongSide,
			width: this.detail.computedShortSide,
		}
	}
	_computeRotatedDetailsInVertical() {
		let detailShortSide = this.detail.computedShortSide
		let detailLongSide = this.detail.computedLongSide

		let detailHeight = detailLongSide
		let detailWidth = detailShortSide

		let atShortSide = Math.floor(this._scrim.computedShortSide / detailLongSide)

		let countInModBottom = BaseDetails.computeCountInMod(this._scrim.computedLongSide, detailHeight, detailWidth)

		let countRotatedInLongSide = Math.floor(this._scrim.computedShortSide / detailLongSide)

		let totalCountRotated = BaseDetails.computeTotalCountInScrim(countInModBottom, countRotatedInLongSide)

		return {
			atShortSide: atShortSide,
			atLongSide: 0,

			totalCount: totalCountRotated,

			countInModShortSide: countInModBottom,
			countInModLongSide: 0,

			height: detailShortSide,
			width: detailLongSide,
		}
	}

	_computeBaseDetailsInHorizontal () {
		let atShortSide = Math.floor(this._scrim.computedShortSide / this.detail.computedLongSide)
		let atLongSide = Math.floor(this._scrim.computedLongSide / this.detail.computedShortSide)

		return {
			atShortSide: atShortSide,
			atLongSide: atLongSide,
			totalCount: BaseDetails.computeTotalCountInScrim(atShortSide, atLongSide),
			height: this.detail.computedShortSide,
			width: this.detail.computedLongSide,
		}
	}
	_computeRotatedDetailsInHorizontal() {
		let detailShortSide = this.detail.computedShortSide
		let detailLongSide = this.detail.computedLongSide

		let detailHeight = detailShortSide
		let detailWidth = detailLongSide

		let atLongSide = Math.floor(this._scrim.computedLongSide / detailLongSide)

		let countInModRight = BaseDetails.computeCountInMod(this._scrim.computedShortSide, detailLongSide, detailShortSide)

		let countRotatedInLongSide = Math.floor(this._scrim.computedLongSide / detailLongSide)

		let totalCountRotated = BaseDetails.computeTotalCountInScrim(countInModRight, countRotatedInLongSide)

		return {
			atShortSide: 0,
			atLongSide: atLongSide,

			totalCount: totalCountRotated,

			countInModShortSide: 0,
			countInModLongSide: countInModRight,

			height: detailLongSide,
			width: detailShortSide,
		}
	}

	_computeModScrim () {
		return {
			atModLongSide: Math.floor(this._computeMods().atShortSide / this._baseDetail.height),
			atModShortSide: Math.floor(this._computeMods().atLongSide / this._baseDetail.width),
		}
	}
	_computeCountRotatedDetailInScrim () {
		let countInModLongSide = this._computeModScrim().atModLongSide
		let countInModShortSide = this._computeModScrim().atModShortSide

		let computeCountLongSide = Math.floor(this._scrim.computedLongSide / this._baseDetail.width)
		let computeCountShortSide = Math.floor(this._scrim.computedShortSide / this._baseDetail.height)
		return {
			atLongSide: countInModLongSide ? computeCountLongSide : 0,
			atShortSide: countInModShortSide ? computeCountShortSide : 0,
		}
	}
	_computeRotatedDetails() {

		let countInModLongSide = this._computeModScrim().atModLongSide
		let countInLongSide = this._computeCountRotatedDetailInScrim().atLongSide
		let countInModShortSide = this._computeModScrim().atModShortSide
		let countInShortSide = this._computeCountRotatedDetailInScrim().atShortSide

		let computeTotalCountLongSide = countInModLongSide ? countInModLongSide * countInLongSide : 0
		let computeTotalCountShortSide = countInModShortSide ? countInModShortSide * countInShortSide : 0


		return {
			height: this._baseDetail.width,
			width: this._baseDetail.height,
			... this._computeCountRotatedDetailInScrim(),
			totalCount: computeTotalCountLongSide ? computeTotalCountLongSide : computeTotalCountShortSide,
			countInModLongSide: countInModLongSide,
			countInModShortSide: countInModShortSide,
		}
	}

	_computeMods() {
		return {
			atLongSide: BaseDetails.computeMod(this._scrim.computedLongSide, this._baseDetail.height),
			atShortSide: BaseDetails.computeMod(this._scrim.computedShortSide, this._baseDetail.width)
		}
	}

	static computeMod(length, detailLengthA) {
		return length % detailLengthA
	}

	static computeCountInMod(length, detailLengthA, detailLengthB) {
		let mod = BaseDetails.computeMod(length, detailLengthA)
		return Math.floor(mod / detailLengthB)
	}

	static computeTotalCountInScrim(detailA, detailB) {
		return detailA * detailB
	}

	get getBaseDetailVertical() {
		return this._baseDetailVertical
	}

	get getRotatedDetailVertical() {
		return this._rotatedDetailVertical
	}

	get getBaseDetailHorizontal() {
		return this._baseDetailHorizontal
	}

	get getRotatedDetailHorizontal() {
		return this._rotatedDetailHorizontal
	}

	get getBaseDetail(){
		return this._baseDetail
	}

	get getRotatedDetail(){
		return this._rotatedDetail
	}
	get getTotalCount() {
		return this._baseDetail.totalCount + this._rotatedDetail.totalCount
	}
	get getTotalCountHorizontal() {
		return this._baseDetailVertical.totalCount + this._rotatedDetailVertical.totalCount
	}
	get getTotalCountVertical() {

		return this._baseDetailHorizontal.totalCount + this._rotatedDetailHorizontal.totalCount
	}
}