import Vue from 'vue'
import ScrimWithDetail from '../plugins/scrimWithDetail/scrimWithDetail';

export default {
	state: {
		details: [
			{
				name: '0 x 0',
				width: 0,
				height: 0,
				indents: {
					left: {
						name: 'Слева',
						value: 2
					},
					right: {
						name: 'Справа',
						value: 2
					},
					bottom: {
						name: 'Снизу',
						value: 2
					},
					top: {
						name: 'Сверху',
						value: 2
					}
				}
			}
		],
		scrims: [
			{
				name: '0 x 0',
				width: 0,
				height: 0
			},
		],
		scrimIndents: [
			{
				name: 'Офсет',
				values: {
					top: 0,
					right: 0,
					bottom: 0,
					left: 0
				}
			},
			{
				name: 'Цифра',
				values: {
					top: 0,
					right: 0,
					bottom: 0,
					left: 0
				}
			},
		],
		colorDetail: {hex: '#fff'},
		colorIndent: {hex: '#fff'},
		colorScrimIndent: {hex: '#fff'},
		colorContentBackground: {hex: '#fff'},
	},
	mutations: {
		setScrims(state, payload){
			state.scrims = payload
		},
		setDetails(state, payload){
			state.details = payload
		},
		setScrimIndents(state, payload){
			state.scrimIndents = payload
		},
		setColorDetail(state, payload){
			state.colorDetail = payload
		},
		setColorIndent(state, payload){
			state.colorIndent = payload
		},
		setColorScrimIndent(state, payload){
			state.colorScrimIndent = payload
		},
		setColorContentBackground(state, payload){
			state.colorContentBackground = payload
		}
	},
	actions: {
		setColorDetail({commit, dispatch}, payload) {
			dispatch('sendUserData', { colorDetail: payload })
			commit('setColorDetail', payload)
		},
		setColorIndent({commit, dispatch}, payload) {
			dispatch('sendUserData', { colorIndent: payload })
			commit('setColorIndent', payload)
		},
		setColorScrimIndent({commit, dispatch}, payload) {
			dispatch('sendUserData', { colorScrimIndent: payload })
			commit('setColorScrimIndent', payload)
		},
		setColorContentBackground({commit, dispatch}, payload) {
			// console.log(payload)
			dispatch('sendUserData', { colorContentBackground: payload })
			commit('setColorContentBackground', payload)
		},
		loadTemplateScrims({commit}, payload) {
			commit('setProcessingToState', true)
			Vue.$db.collection('userData')
				.doc(payload)
				.get()
				.then(querySnapshot => {
					let data = querySnapshot.data() ? querySnapshot.data(): []
					let scrimDefaultIndents = {
						width: data.scrimIndents[0].values.right + data.scrimIndents[0].values.left,
						height: data.scrimIndents[0].values.bottom + data.scrimIndents[0].values.top,
					}
					data.scrims.forEach((item, index, arr) => {
						arr[index].offsetWidth = scrimDefaultIndents.width
						arr[index].offsetHeight = scrimDefaultIndents.height

					})
					commit('setScrims', data.scrims)
					commit('setScrimIndents', data.scrimIndents)
					commit('setDetails', data.details)
					commit('setColorDetail', data.colorDetail)
					commit('setColorIndent', data.colorIndent)
					commit('setColorScrimIndent', data.colorScrimIndent)
					commit('setColorContentBackground', data.colorContentBackground)
					commit('setScrimIndent', data.scrimIndents[0])
				}).catch(error => {
				commit('setProcessingToState', false)
				console.log(error)
			})
		}
	},
	getters: {
		details(state) {
			return state.details
		},
		scrims(state) {
			return state.scrims
		},
		getScrims(state) {
			return state.scrims
		},
		getDetails(state) {
			return state.details
		},

		colorDetail(state) {
			return state.colorDetail
		},
		colorIndent(state) {
			return state.colorIndent
		},
		colorScrimIndent(state) {
			return state.colorScrimIndent
		},
		colorContentBackground(state) {
			return state.colorContentBackground
		},


		scrimIndents(state) {
			return state.scrimIndents
		}
	}
}