import Scrim from './scrimAndDetails/scrim'
import BaseDetails from './scrimAndDetails/baseDetails'
// import RotatedDetail from './scrimAndDetails/rotatedDetail'

export default class ScrimWithDetail {
	constructor(inputData = {
		scrimWidth: 1,
		scrimHeight: 1,
		offsetWidth: 1,
		offsetHeight: 1,
		detailWidth: 1,
		detailHeight: 1,
		detailIndentLeft: 1,
		detailIndentRight: 1,
		detailIndentTop: 1,
		detailIndentBottom: 1,
		showRotated: true
	}) {
		this._inputData = inputData
		this.scrim = new Scrim(this._inputData)
		this.baseDetail = new BaseDetails(this._inputData)
		// this.rotatedDetail = new RotatedDetail(inputData)

	}
	set setScrim(payload) {
		this._inputData.scrimHeight = payload.scrimHeight ? payload.scrimHeight : this._inputData.scrimHeight
		this._inputData.scrimWidth = payload.scrimWidth ? payload.scrimWidth : this._inputData.scrimWidth
	}
	set setDetail(payload) {
		// todo сделать цикл
		this._inputData.detailWidth = payload.detailWidth ? payload.detailWidth : this._inputData.detailWidth
		this._inputData.detailHeight = payload.detailHeight ? payload.detailHeight : this._inputData.detailHeight
		this._inputData.detailIndentLeft = payload.detailIndentLeft ? payload.detailIndentLeft : this._inputData.detailIndentLeft
		this._inputData.detailIndentRight = payload.detailIndentRight ? payload.detailIndentRight : this._inputData.detailIndentRight
		this._inputData.detailIndentTop = payload.detailIndentTop ? payload.detailIndentTop : this._inputData.detailIndentTop
		this._inputData.detailIndentBottom = payload.detailIndentBottom ? payload.detailIndentBottom : this._inputData.detailIndentBottom
	}
	set setInputData(payload) {
		this._inputData = payload
	}
	get getSizes() {
		return {
			scrim: {
				... this.scrim.getScrimSizes,
				// totalCountDetails: this.baseDetail.getTotalCount + this.rotatedDetail.getTotalCount
				totalCountDetails: this.baseDetail.getTotalCount
			},
			details: {
				baseDetail: this.baseDetail.getBaseDetail,
				// rotatedDetail: 1,
				rotatedDetail: this.baseDetail.getRotatedDetail,
			}
		}
	}
	get getDetailSizesInScrimShortSide () {
		return {
			scrim: {
				... this.scrim.getScrimSizes,
				// totalCountDetails: this.baseDetail.getTotalCount + this.rotatedDetail.getTotalCount
				totalCountDetails: this.baseDetail.getTotalCountHorizontal
			},
			details: {
				baseDetail: this.baseDetail.getBaseDetailVertical,
				rotatedDetail: this.baseDetail.getRotatedDetailVertical,
			}
		}
	}
	get getDetailSizesInScrimLongSide () {
		return {
			scrim: {
				... this.scrim.getScrimSizes,
				// totalCountDetails: this.baseDetail.getTotalCount + this.rotatedDetail.getTotalCount
				totalCountDetails: this.baseDetail.getTotalCountVertical
			},
			details: {
				baseDetail: this.baseDetail.getBaseDetailHorizontal,
				rotatedDetail: this.baseDetail.getRotatedDetailHorizontal,
			}
		}
	}

}