export default class StoreScrimAndDetail {
	constructor (inputData) {
		// detail sizes
		this.detail = {
			inputWidth: inputData.detailWidth + inputData.detailIndentTop + inputData.detailIndentBottom,
			inputHeight: inputData.detailHeight + inputData.detailIndentLeft + inputData.detailIndentRight
		}
		const inputDetailSizes = [this.detail.inputWidth, this.detail.inputHeight]

		this.detail.computedLongSide = StoreScrimAndDetail.getMaxSize(inputDetailSizes)
		this.detail.computedShortSide = StoreScrimAndDetail.getMinSize(inputDetailSizes)

		this.detail.baseDetail = {}

		this.detail.rotatedDetail = {
			totalCount: Number
		}

		// scrim sizes
		this._scrim = {
			inputWidth: inputData.scrimWidth,
			inputHeight: inputData.scrimHeight,
		}
		const inputScrimSizes = [this._scrim.inputWidth, this._scrim.inputHeight]

		this._scrim.computedLongSide = StoreScrimAndDetail.getMaxSize(inputScrimSizes) - Number(inputData.offsetWidth)
		this._scrim.computedShortSide = StoreScrimAndDetail.getMinSize(inputScrimSizes) - Number(inputData.offsetWidth)

		this._scrim.isMaxSide = this._scrim.computedLongSide === this._scrim.inputWidth
		this._scrim.isMinSide = this._scrim.computedShortSide === this._scrim.inputHeight

		this._scrim.mods = {}
	}

	set setBaseDetail(data) {
		this.detail.baseDetail = data
	}

	static getMaxSize(elements) {
		let max = elements[0]
		elements.forEach((el)=>{
			if (el >= max) max = el
		})
		return max
	}

	static getMinSize(elements) {
		let min = elements[0]
		elements.forEach((el)=>{
			if (el <= min) min = el
		})
		return min
	}
}