<template>
	<v-toolbar
			clipped-left
			app
			fixed
	>
		<v-toolbar-title>
			<i class="icon-logo"></i>
		</v-toolbar-title>
		<v-spacer></v-spacer>
		<v-toolbar-items class="hidden-sm-and-down">
			<v-btn
					flat
					to="/sign-up"
					v-if="!getUserAuth"
			>Регистрация</v-btn>
			<v-btn
					flat
					to="/sign-in"
					v-if="!getUserAuth"
			>Войти</v-btn>
			<v-btn
					flat
					@click.prevent="signOut()"
					v-if="getUserAuth"
			>Выйти</v-btn>

			<v-menu
					offset-y
					min-width="250"
					:close-on-content-click="false"
			>
				<template v-slot:activator="{ on }">
					<v-btn
							icon
							v-on="on"
					>
						<v-icon>more_vert</v-icon>
					</v-btn>
				</template>
				<v-card>
					<v-card-actions>
						<v-spacer></v-spacer>
						<v-switch
								@change="setDark($event)"
								primary
								:label="getDark ? 'Ночь' : 'День'"
								:input-value="getDark"
								messages=""
						/>
					</v-card-actions>

				</v-card>
			</v-menu>
		</v-toolbar-items>
	</v-toolbar>
</template>

<script>
	import {mapGetters, mapActions} from 'vuex'

	export default {
		name: 'MainHeader',
		data: () => ({
		}),
		computed: {
			...mapGetters([
				'getDark',
				'getUserAuth'
			])
		},
		methods: {
			...mapActions([
				'setDark',
				'signOut'
			])
		}
	}
</script>

<style scoped>

</style>