import StoreScrimAndDetail from './storeScrimAndDetail';

export default class Scrim extends StoreScrimAndDetail {
	constructor(props) {
		super(props);
		this._scrimSizes = {
			longSide: this._scrim.computedLongSide,
			shortSide: this._scrim.computedShortSide,
			offsetHeight: Number(props.offsetHeight),
			offsetWidth: Number(props.offsetWidth),
		}
	}
	get getScrimSizes() {
		return this._scrimSizes
	}
}