<template>
  <v-app id="sandbox" :dark="getDark">
    <main-header></main-header>
    <router-view></router-view>
  </v-app>
</template>

<script>
  import MainHeader from './components/MainHeader'
  import {mapGetters, /*mapActions*/} from 'vuex'

  export default {
    data: () => ({

    }),
    computed: {
      ...mapGetters([
        'getDark',
        'getUserAuth'
      ])
    },
    components: {
      MainHeader
    },
    watch: {
      getUserAuth(val) {
        if (val) {
          this.$router.push('/')
        } else {
          this.$router.push('/sign-in')
        }
      }
    },
  }
</script>
